const isEnabled = (requested, disponibles = []) => requested.every(flag => disponibles.indexOf(flag) !== -1)

export default ({  disponibles }, enable = true) => {
  const loading = typeof disponibles === 'undefined'
  const Flagger = ({ children, fallback, feature = [] }) => {
    let requested = [].concat(feature)
    const flag = enable == isEnabled(requested, disponibles)
    Flagger.displayName =
      'Feature.' + (enable ? 'Enabled' : 'Disabled') + '[' + requested.join(',') + ']' + ' = [' + loading ? 'loading...' : disponibles.join(',') + '] '
    if (loading) {
      return fallback || null
    } else {
      return flag ? children : null
    }
  }
  return Flagger
}
