import React from 'react'
import { Theme, Reset } from '@resultadosdigitais/tangram-components'
import { Connect, withContext } from '@resultadosdigitais/front-hub/react'
import loginService from './login-service'

import Login from './components/Login'

function App(props) {
  const csrfToken = props.csrf.token
  loginService.initialize({ csrfToken })

  return (
    <Theme value={'marketing'}>
      <Reset />
      <Login />
    </Theme>
  )
}

export default Connect(withContext(App))
