export const isNull = o => o === null

export const throwError = (message, err = '') => {
  throw new Error(`front-hub: ${message}\n\n${err}`)
}

export const isPromise = p => p instanceof Promise || (p && typeof p.then === 'function')
export const isFunction = f => typeof f === 'function'
export const isValid = o => !isNull(o)
export const isObject = o => !isNull(o) && typeof o === 'object' && o.constructor === Object

export function merge() {
  return mutate({}).apply(void 0, arguments)
}

export function mutate(target) {
  return function self() {
    const ret = {}
    Array.from(arguments).forEach(function merge(argument, i) {
      !isValid(argument) && throwError(`${i} paramter must be a valid object`, argument)
      Object.entries(argument).forEach(function mutateItem([key, value]) {
        if (isObject(value)) {
          ret[key] = target[key] = self(target[key] || {}, value)
        } else if (Array.isArray(value)) {
          ret[key] = target[key] = [...value]
        } else {
          ret[key] = target[key] = value
        }
      })
    })
    return ret
  }
}
