import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import loginService from '../../login-service'

import Link from '../Link'
import Button from '../Button'
import Input from '../Input'
import PasswordInput from '../PasswordInput'
import InputLabel from '../InputLabel'
import Form from '../Form'
import FormControl from '../FormControl'
import Alert from '../Alert'
import Spacer from '../Spacer'

import Header from './Header'
import Bottom from './Bottom'
import { validate, isEmail, isEmpty, hasLength } from '../../validation'
import isEmptyObject from './isEmptyObject'

function Login({ onPasswordRecoverClick }) {
  const { t } = useTranslation()

  const [formState, setFormState] = useState('idle')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [validation, setValidation] = useState()

  const schema = {
    email: [
      {
        method: isEmpty,
        validWhen: false,
        message: t('LoginForm.EmptyField'),
      },
      {
        method: isEmail,
        validWhen: true,
        message: t('LoginForm.InvalidEmailField'),
      },
    ],
    password: [
      {
        method: isEmpty,
        validWhen: false,
        message: t('LoginForm.EmptyField'),
      },
      {
        method: value => hasLength(value, { minimum: 6 }),
        validWhen: true,
        message: t('LoginForm.RequiredLength'),
      },
    ],
  }

  const isSubmiting = formState === 'submiting'
  const isError = formState === 'error'

  function handlePasswordRecoverClick(event) {
    event.preventDefault()
    onPasswordRecoverClick()
  }

  function handleEmailChange(event) {
    event.preventDefault()

    if (isSubmiting) return

    setEmail(event.target.value)

    if (validation?.email) setValidation(validate({ email: event.target.value }, schema))
  }

  function handlePasswordChange(event) {
    event.preventDefault()

    if (isSubmiting) return

    setPassword(event.target.value)

    if (validation?.password) setValidation(validate({ password: event.target.value }, schema))
  }

  function handleFormSubmit(event) {
    event.preventDefault()

    if (isSubmiting) return

    const validation = validate({ email, password }, schema)
    setValidation(validation)

    if (isEmptyObject(validation)) submitForm()
  }

  async function submitForm() {
    setFormState('submiting')

    try {
      const { data } = await loginService.login({ email, password })
      window.location.assign(data.redirectTo)
    } catch (e) {
      console.error(e)
      setFormState('error')
    }
  }

  return (
    <Form onSubmit={handleFormSubmit} noValidate disabled={isSubmiting}>
      <Spacer height="--size-spacing-07" />

      <Header />

      <FormControl errorMessage={validation?.email} disabled={isSubmiting}>
        <InputLabel htmlFor="email">{t('LoginForm.Email')}</InputLabel>
        <Input type="email" id="email" name="email" autoComplete="username" value={email} onChange={handleEmailChange} readOnly={isSubmiting} />
      </FormControl>

      <FormControl errorMessage={validation?.password} disabled={isSubmiting}>
        <InputLabel htmlFor="password">{t('LoginForm.Password')}</InputLabel>
        <PasswordInput
          id="password"
          name="password"
          autoComplete="current-password"
          value={password}
          onChange={handlePasswordChange}
          readOnly={isSubmiting}
        />
      </FormControl>

      <Link href="#" target="_blank" onClick={handlePasswordRecoverClick}>
        {t('LoginForm.ForgotYourPassword')}
      </Link>

      <Spacer height="--size-spacing-04" />

      {isError && <Alert severity="error">{t('LoginForm.WrongCredentials')}</Alert>}

      <Spacer height="--size-spacing-04" />

      <Button type="submit" loading={isSubmiting}>
        {t('LoginForm.ButtonNext')}
      </Button>

      <Spacer height="--size-spacing-07" />

      <Bottom />

      <Spacer height="--size-spacing-05" />
    </Form>
  )
}

export default Login
