import React, { useState } from 'react'
import styled from 'styled-components'
import { Logo } from '@resultadosdigitais/tangram-components'
import { useTranslation } from 'react-i18next'
import { CSSTransition } from 'react-transition-group'

import { motionDurationNormal } from '@resultadosdigitais/tangram-design-tokens'
import RDStationLogo from './logo-rdstation.svg'
import { usePath } from '@resultadosdigitais/front-hub/react'
import LoginForm from '../LoginForm'
import RecoverPasswordForm from '../PasswordRecoverForm'

const Header = styled.header`
  display: flex;
  width: 100%;
  justify-content: space-between;
  background-color: var(--color-white);
  box-shadow: var(--shadow-md);
`

const Root = styled.div`
  background: var(--color-gray-10);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  padding-top: var(--size-layout-05);
`

const Box = styled.div`
  position: relative;
  width: 380px;
`

const FadeLeftPanel = styled.div`
  &.enter {
    opacity: 0;
    transform: translateX(-100%);
  }

  &.enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: all ${props => props.timeout}ms;
  }

  &.enter-done {
    opacity: 1;
    transform: translateX(0);
  }

  &.exit {
    opacity: 1;
    transform: translateX(0);
  }

  &.exit-active {
    opacity: 0;
    transform: translateX(-100%);
    transition: all ${props => props.timeout}ms;
  }
`

const FadeRightPanel = styled.div`
  opacity: 0;

  &.enter {
    opacity: 0;
    transform: translateX(100%);
  }

  &.enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: all ${props => props.timeout}ms;
  }

  &.enter-done {
    opacity: 1;
    transform: translateX(0);
  }

  &.exit {
    opacity: 1;
    transform: translateX(0);
  }

  &.exit-active {
    opacity: 0;
    transform: translateX(100%);
    transition: all ${props => props.timeout}ms;
  }
`

function Login() {
  const { t } = useTranslation()
  const assetPath = usePath()
  const [currentForm, setCurrentForm] = useState('LoginForm')

  function goToPasswordRecoverForm() {
    setCurrentForm('RecoverPasswordForm')
  }

  function goToLoginForm() {
    setCurrentForm('LoginForm')
  }

  let timeout = Number(motionDurationNormal.replace('ms', ''))

  return (
    <>
      <Header>
        <Logo rel="noopener noreferrer" href="/">
          <img src={assetPath(RDStationLogo)} alt="RDStation Logo" />
        </Logo>
      </Header>
      <Root>
        <Box>
          <CSSTransition in={currentForm === 'LoginForm'} timeout={timeout} unmountOnExit>
            <FadeLeftPanel timeout={timeout}>
              <LoginForm onPasswordRecoverClick={goToPasswordRecoverForm} />
            </FadeLeftPanel>
          </CSSTransition>
          <CSSTransition in={currentForm === 'RecoverPasswordForm'} timeout={timeout} unmountOnExit>
            <FadeRightPanel timeout={timeout}>
              <RecoverPasswordForm onBackButtonClick={goToLoginForm} />
            </FadeRightPanel>
          </CSSTransition>
        </Box>
      </Root>
    </>
  )
}

export default Login
