import { useContext, useState, useEffect } from 'react'

import { FronthubContext } from 'front-hub-context-api'
import Flagger from './flagger'
import { isPromise } from '../../../../core/object'

export default function useFeature() {
  const { features } = useContext(FronthubContext)
  const [featuresDisponibles, setFeaturesDisponibles] = useState(features)

  let disponibles
  let cancel

  if (isPromise(featuresDisponibles)) {
    cancel = featuresDisponibles.then(setFeaturesDisponibles).cancel
  } else {
    disponibles = Object.entries(featuresDisponibles).map(([_, value]) => value)
  }
  useEffect(() => cancel, [])

  const Component = Flagger({ disponibles })
  Component.Enabled = Flagger({ disponibles })
  Component.Disabled = Flagger({ disponibles }, false)

  return Component
}
