import React, { useState } from 'react'
import styled from 'styled-components'
import { EyeOpen, EyeClose } from '@resultadosdigitais/tangram-react-icons'

import Input from '../Input'

const Container = styled.div`
  position: relative;
`

const AddornmentRight = styled.div`
  position: absolute;
  top: 50%;
  right: 5px;
  margin-top: -12px;
  cursor: pointer;
`

function PasswordInput(props) {
  const [showPassword, setShowPassword] = useState(false)

  function handleToggleShowPassword(event) {
    event.preventDefault()
    setShowPassword(currentShowPassword => !currentShowPassword)
  }

  return (
    <Container>
      <Input type={showPassword ? 'text' : 'password'} {...props} />
      <AddornmentRight onClick={handleToggleShowPassword}>
        {showPassword && <EyeOpen />}
        {!showPassword && <EyeClose />}
      </AddornmentRight>
    </Container>
  )
}

export default PasswordInput
