/* eslint-disable import/no-unresolved */
/* eslint-disable max-classes-per-file */
// eslint-disable-next-line import/no-unresolved
/* globals DISABLE_LOCALIZATION */
import React from 'react'

import contextHubApi from '../context-hub-api'
import watchURLChangesFactory from './watch-url-changes'
import RootCompoment from './root-compoment'

export default function rootCompomentBuilder(fronthubInstance, MicrofrontendComponent, Router) {
  const contextPublicApi = contextHubApi(fronthubInstance)
  const watchURLChanges = watchURLChangesFactory(contextPublicApi)
  let localizedMicrofrontendComponent

  if (DISABLE_LOCALIZATION) {
    localizedMicrofrontendComponent = MicrofrontendComponent
  } else {
    // eslint-disable-next-line global-require
    localizedMicrofrontendComponent = require('../i18n-hoc').default(MicrofrontendComponent, fronthubInstance.configuration) // tree shaking
  }
  return ({ params }) => (
    <RootCompoment
      MicrofrontendComponent={localizedMicrofrontendComponent}
      Router={Router}
      WatchURLChanges={watchURLChanges}
      params={params}
      contextPublicApi={contextPublicApi}
    />
  )
}
