import React, { useState } from 'react'
import styled from 'styled-components'
import { Text } from '@resultadosdigitais/tangram-components'
import { useTranslation } from 'react-i18next'
import { ChevronLeft } from '@resultadosdigitais/tangram-react-icons'
import loginService from '@resultadosdigitais/accounts-frontend-login-service'

import Alert from '../Alert'
import Link from '../Link'
import Button from '../Button'
import Input from '../Input'
import InputLabel from '../InputLabel'
import Form from '../Form'
import FormControl from '../FormControl'
import Spacer from '../Spacer'
import { validate, isEmail, isEmpty } from '../../validation'
import isEmptyObject from './isEmptyObject'

const StyledLink = styled(Link)`
  font-size: var(--text-size-md);
`

function RecoverPassword({ onBackButtonClick, timeout }) {
  const { t } = useTranslation()

  const [formState, setFormState] = useState('idle')
  const [email, setEmail] = useState('')
  const [validation, setValidation] = useState()

  const schema = {
    email: [
      {
        method: isEmpty,
        validWhen: false,
        message: t('LoginForm.EmptyField'),
      },
      {
        method: isEmail,
        validWhen: true,
        message: t('LoginForm.InvalidEmailField'),
      },
    ],
  }

  const isIdle = formState === 'idle'
  const isSubmiting = formState === 'submiting'
  const isSuccess = formState === 'success'
  const isError = formState === 'error'

  function handleBackButtonClick(event) {
    event.preventDefault()
    onBackButtonClick()
  }

  function handleEmailChange(event) {
    event.preventDefault()

    if (isSubmiting) return

    setEmail(event.target.value)

    if (validation?.email) setValidation(validate({ email: event.target.value }, schema))
  }

  function handleFormSubmit(event) {
    event.preventDefault()

    if (isSubmiting) return

    const validation = validate({ email }, schema)
    setValidation(validation)

    if (isEmptyObject(validation)) submitForm()
  }

  async function submitForm() {
    setFormState('submiting')

    try {
      await loginService.recoverPassword({ email })
      setFormState('success')
    } catch (e) {
      console.error(e)
      setFormState('error')
    }
  }

  return (
    <Form onSubmit={handleFormSubmit} noValidate disabled={isSubmiting}>
      <Spacer height="--size-spacing-07" />

      <StyledLink
        startIcon={<ChevronLeft title={t('PasswordRecoverForm.BackIcon')} titleId="backIcon" />}
        href="#"
        onClick={handleBackButtonClick}
        removeUnderline
      >
        {t('PasswordRecoverForm.BackButtonLabel')}
      </StyledLink>

      <Spacer height="--size-spacing-07" />

      <Text token={Text.tokens.FONT_SIZE_XXL_BOLD}>{t('PasswordRecoverForm.Title')}</Text>

      <Spacer height="--size-spacing-02" />

      <Text token={Text.tokens.FONT_SIZE_MD_SEMIBOLD} textColor="--color-gray-70">
        {t('PasswordRecoverForm.Description')}
      </Text>

      {(isIdle || isSubmiting) && <Spacer height="--size-spacing-07" />}

      {isSuccess && (
        <>
          <Alert severity="success">{t('PasswordRecoverForm.Success')}</Alert>
          <Spacer height="--size-spacing-03" />
        </>
      )}

      {isError && (
        <>
          <Alert severity="error">{t('PasswordRecoverForm.DefaultError')}</Alert>
          <Spacer height="--size-spacing-03" />
        </>
      )}

      <FormControl errorMessage={validation?.email} disabled={isSubmiting}>
        <InputLabel htmlFor="email">{t('LoginForm.Email')}</InputLabel>
        <Input type="email" id="email" name="email" autoComplete="username" value={email} onChange={handleEmailChange} readOnly={isSubmiting} />
      </FormControl>

      <Spacer height="--size-spacing-04" />

      <Button type="submit" loading={isSubmiting}>
        {t('PasswordRecoverForm.ConfirmButton')}
      </Button>

      <Spacer height="--size-spacing-06" />
    </Form>
  )
}

export default RecoverPassword
