import { parseToSafeJson } from '../../core/security'

export default function fronthubComunicationApiFactory(fronthubInstance) {
  return {
    dispatchEvent(eventName, eventData) {
      ;(fronthubInstance.listeners[eventName] || []).forEach(listener => listener.call(undefined, eventData))
    },
    delegate(delegateName, callback) {
      if (Object.prototype.hasOwnProperty.call(fronthubInstance.delegates, delegateName)) {
        callback.call(this, parseToSafeJson(fronthubInstance.delegates[delegateName].call(undefined)))
      } else {
        // eslint-disable-next-line no-param-reassign
        fronthubInstance.deferedDelegates[delegateName] = callback
      }
    },
    registerDelegate(delegateName, delegate) {
      if (!Object.prototype.hasOwnProperty.call(fronthubInstance.delegates, delegateName)) {
        // eslint-disable-next-line no-param-reassign
        fronthubInstance.delegates[delegateName] = delegate
      }
      if (Object.prototype.hasOwnProperty.call(fronthubInstance.deferedDelegates, delegateName)) {
        fronthubInstance.deferedDelegates[delegateName].call(undefined, delegate)
      }
    },
    addEventListener(eventName, callback) {
      if (!Object.prototype.hasOwnProperty.call(fronthubInstance.listeners, eventName)) {
        // eslint-disable-next-line no-param-reassign
        fronthubInstance.listeners[eventName] = []
      }
      fronthubInstance.listeners[eventName].push(callback)
    },
  }
}
